<h1 id="access-token" tabindex="-1"><a class="header-anchor" href="#access-token" aria-hidden="true">#</a> Access Token</h1>
<p>SDK 中有一个 <a href="https://github.com/overtrue/wechat/blob/master/src/Core/AccessToken.php" target="_blank" rel="noopener noreferrer">Access Token<OutboundLink/></a> 对象，它是一个全局使用的东西，请把它与 OAuth 中的 code 换取的 Access Token 区别开。</p>
<p>我们一个 SDK 应用在初始化以后，你可以在任何时机从应用中拿到该配置下的 Access Token 实例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Foundation<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>

<span class="token variable">$options</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token comment">//...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$options</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 获取 access token 实例</span>
<span class="token variable">$accessToken</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">access_token</span><span class="token punctuation">;</span> <span class="token comment">// EasyWeChat\Core\AccessToken 实例</span>
<span class="token variable">$token</span> <span class="token operator">=</span> <span class="token variable">$accessToken</span><span class="token operator">-></span><span class="token function">getToken</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// token 字符串</span>
<span class="token variable">$token</span> <span class="token operator">=</span> <span class="token variable">$accessToken</span><span class="token operator">-></span><span class="token function">getToken</span><span class="token punctuation">(</span><span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 强制重新从微信服务器获取 token.</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div><h2 id="修改-app-的-access-token" tabindex="-1"><a class="header-anchor" href="#修改-app-的-access-token" aria-hidden="true">#</a> 修改 <code>$app</code> 的 Access Token</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'access_token'</span><span class="token punctuation">]</span><span class="token operator">-></span><span class="token function">setToken</span><span class="token punctuation">(</span><span class="token variable">$newAccessToken</span><span class="token punctuation">,</span> <span class="token variable">$expires</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>例如：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'access_token'</span><span class="token punctuation">]</span><span class="token operator">-></span><span class="token function">setToken</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'ccfdec35bd7ba359f6101c2da321d675'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或者指定过期时间</span>
<span class="token variable">$app</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'access_token'</span><span class="token punctuation">]</span><span class="token operator">-></span><span class="token function">setToken</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'ccfdec35bd7ba359f6101c2da321d675'</span><span class="token punctuation">,</span> <span class="token number">3600</span><span class="token punctuation">)</span><span class="token punctuation">;</span>  <span class="token comment">// 单位：秒</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="设置-accesstoken-的缓存" tabindex="-1"><a class="header-anchor" href="#设置-accesstoken-的缓存" aria-hidden="true">#</a> 设置 AccessToken 的缓存</h2>
<p>你也可以自定义 token 的缓存方式，把一个实现了 <code>Doctrine\Common\Cache\Cache</code> 缓存接口的实例作为 AccessToken 构造函数的第三个参数传入即可：</p>
<p>本项目使用 <a href="https://github.com/doctrine/cache" target="_blank" rel="noopener noreferrer">doctrine/cache<OutboundLink/></a> 来完成缓存工作，它支持几乎目前所有的缓存引擎。</p>
<p>以 Redis 为例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code>
<span class="token keyword">use</span> <span class="token package">Doctrine<span class="token punctuation">\</span>Common<span class="token punctuation">\</span>Cache<span class="token punctuation">\</span>RedisCache</span><span class="token punctuation">;</span> <span class="token comment">// RedisCache 实例了 `Doctrine\Common\Cache\Cache` 接口</span>

<span class="token variable">$cache</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">RedisCache</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 创建 redis 实例</span>
<span class="token variable">$redis</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Redis</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$redis</span><span class="token operator">-></span><span class="token function">connect</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'redis_host'</span><span class="token punctuation">,</span> <span class="token number">6379</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$cache</span><span class="token operator">-></span><span class="token function">setRedis</span><span class="token punctuation">(</span><span class="token variable">$redis</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">access_token</span><span class="token operator">-></span><span class="token function">setCache</span><span class="token punctuation">(</span><span class="token variable">$cache</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div>